<template>
  <v-app>
    <v-navigation-drawer
      app
      v-model="showNavigator"
      disable-resize-watcher
      class="elevation-4"
      z-index="10"
    >
      <my-nav-list
        @changeTag="changeTag"
        :tag="tag"
        :navigatorInfo="navigatorInfo"
        :expand="expand"
      ></my-nav-list>
      <!-- <page-2></page-2> -->
    </v-navigation-drawer>

    <!-- v-if="isShowIcon" -->

    <v-app-bar app>
      <v-btn class="right-return-icon" dark plain @click="openOrClose()"
        ><v-icon color="red">mdi-alien-outline</v-icon></v-btn
      >
    </v-app-bar>

    <!-- 根据应用组件来调整你的内容 -->
    <v-main>
      <!-- 给应用提供合适的间距 -->
      <v-container fluid class="context">
        <!-- 如果使用 vue-router -->
        <router-view />
      </v-container>
    </v-main>

    <v-footer app color="green"> 这是一个底部区域 </v-footer>
  </v-app>
</template>
<script>
import myNavList from "../components/myNavList.vue";
// import page2 from "./page2.vue";
export default {
  components: {
    myNavList,
    // page2,
  },

  data() {
    return {
      navigatorInfo: [],
      right: null,
      showNavigator: false,
      isShowIcon: false,
      tag: "indexPage",
      expand: false,
    };
  },
  watch: {
    "$vuetify.breakpoint.name"(newVal) {
      //监听当前页面的参数变化
      this.autoShowNavigator(newVal);
    },
    showNavigator() {
      this.isShowIcon = !this.showNavigator;
    },
  },
  created() {
    this.autoShowNavigator(this.$vuetify.breakpoint.name);
  },
  mounted() {
    if (this.$router.history.current.path !== "/") {
      this.tag = this.$router.history.current.path.substring(6);
    }
    // if (!!window.sessionStorage.getItem("navP")) {
    //   let a = JSON.parse(window.sessionStorage.getItem("navP"));
    //   console.log(a, "000");
    //   this.navigatorInfo = a;
    // } else {
    //   this.navigatorInfo = this.NavList;
    // }
    this.navigatorInfo = this.NavList;
  },
  methods: {
    changeTag(path) {
      this.tag = path;
    },

    //动态设置左侧导航栏
    autoShowNavigator(val) {
      this.isShowIcon = val != "lg" && val != "xl";
      this.showNavigator = !this.isShowIcon;
    },
    openOrClose() {
      this.showNavigator = this.isShowIcon;
      this.isShowIcon = !this.isShowIcon;
    },
  },
};
</script>
<style >
html {
  width: 100%;
  background-color: #f2f5f8;
}
/*全局滚动条样式*/
::-webkit-scrollbar {
  width: 0px;
  height: 14px;
}

::-webkit-scrollbar-thumb {
  /* background-color: #01c8dc;
border-radius: 3px; */
  background-color: #eb3903;
}
.context {
  height: 100%;
  background: skyblue;
  padding: 8px !important;
}
/*全局滚动条样式结束*/
</style>