          </v-list-item-content>
        </v-list-item>
      </label>
      <label for="" v-else>
        <v-list-group @click="test(i)" :value="item.value">
          <template v-slot:activator>
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ item.name }}</v-list-item-title>
            </v-list-item-content>
          </template>
          <label for="" v-for="(m, k) in item.children" :key="k">
            <label for="" v-if="!m.children">
              <v-list-item
                exact
                active-class="linkActive"
                :to="m.path"
                style="padding-left: 1.5rem"
              >
                <v-list-item-icon>
                  <v-icon v-text="m.icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
