            <label for="" v-else>
              <v-list-group
                :sub-group="true"
                @click="test(i, k)"
                :value="m.value"
              >
                <template v-slot:activator>
                  <v-icon></v-icon>
                  <v-list-item-content>
                    <v-list-item-title style="padding-left: 1rem">{{
                      m.name
                    }}</v-list-item-title>
                  </v-list-item-content>
                </template>
                <label for="" v-for="(b, v) in m.children" :key="v">
                  <v-list-item
                    style="padding-left: 3rem"
