                    <v-list-item-content>
                      <v-list-item-title v-text="b.name"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </label>
              </v-list-group>
            </label>
          </label>
        </v-list-group>
      </label>
    </label>
  </v-list>
</template>

